import { publicRoutes, privateRoutes } from '@/router';

export default {
    namespaced: true,
    state: {
        //  
        routes: publicRoutes
    },
    mutations: {
        /**
         * 
         * @param {*} state
         * @param {*} newRoutes
         */
        setRoutes(state, newRoutes) {
            state.routes = [...publicRoutes, ...newRoutes];
        }
    },
    actions: {
        /**
         * 
         * @param {*} context
         * @param {*} menus 
         */
        filterRoutes(context, menus) {
            // addRoute
            const routes = [];
            menus.forEach((key) => {
                const filterRoute = privateRoutes.filter(
                    (item) => item.name === key
                );
                routes.push(...filterRoute);
            });

            routes.push(
                // 404
                //  
                {
                    path: '/:catchAll(.*)',
                    redirect: '/404'
                }
            );

            context.commit('setRoutes', routes);

            return routes;
        }
    }
};
